import React from 'react'
import { Layout, Card } from 'antd';
import { TeamOutlined, DashboardOutlined, FieldTimeOutlined } from '@ant-design/icons';
import { Tabs } from 'antd';
const { Content } = Layout;
const { TabPane } = Tabs;

const DashboardTab = () => (

    <Tabs defaultActiveKey="1" style={{ backgroundColor: '#dedcde', padding: 10 }}>
        <TabPane
            tab={
                <span>
                    <DashboardOutlined />
                    My Dashboard
                </span>
            }
            key="1">

        </TabPane>
        <TabPane
            tab={
                <span>
                    <TeamOutlined />
                    Employees
                </span>
            }
            key="2">
            Tab 2
        </TabPane>
        <TabPane
            tab={
                <span>
                    <FieldTimeOutlined />
                    Time-off
                </span>
            }
            key="3"
        >
            Tab 2
        </TabPane>
    </Tabs>

);
export default DashboardTab