import { DownOutlined, UserOutlined } from '@ant-design/icons';
import { Dropdown, Menu, Space, Input } from 'antd';
import { Layout, Button, Avatar } from 'antd';
const { Header } = Layout;
const { Search } = Input;

function HeaderMain({ handleLogout }) {
    const onSearch = (value) => console.log(value);
    const menu = (
        <Menu
            items={[
                {
                    label: <a href="https://www.antgroup.com">Profile</a>,
                    key: '0',
                },
                {
                    type: 'divider',
                },
                {
                    label: <a href="https://www.aliyun.com">Change Password</a>,
                    key: '1',
                },
                {
                    type: 'divider',
                },
                {
                    label: <Button type="link" onClick={handleLogout}>Logout</Button>,
                    key: '3',
                },
            ]}
        />);

    return (
        <Header
            className="site-layout-background"
            style={{
                padding: 0,
            }}
        >

            <div className='profile-icon'>
                <Search placeholder="input search text" onSearch={onSearch} style={{
                    width: 200, margin: 15,
                }} enterButton />
                <Dropdown overlay={menu} trigger={['click']}>
                    <a onClick={e => e.preventDefault()}>
                        <Space>
                            <Avatar size={40} icon={<UserOutlined />} />

                        </Space>
                    </a>
                </Dropdown>
            </div>
        </Header>

    )
}

export default HeaderMain